



























import vuetifyColors from 'vuetify/es5/util/colors'
import { Component, Vue, Prop } from 'vue-property-decorator'
import { Sketch } from 'vue-color'

@Component({
  components: {
    Sketch
  }
})
export default class Color extends Vue {
  @Prop({ required: true })
  value!: string

  @Prop({ required: true })
  label!: string

  @Prop({ default: false })
  clearable!: boolean

  @Prop({ default: false })
  alpha!: boolean

  get colors (): string[] {
    return [
      '#ffffff',
      '#cdcdcd',
      '#888888',
      '#303030',
      '#000000'
    ].concat(
      ...Object.values(vuetifyColors)
        .map(({ base }) => base ? [base] : [])
    )
  }
}
