
















import ColorTile from './ColorTile.vue'
import { Component, Emit, Prop, Vue } from 'vue-property-decorator'

type value = string

@Component({
  components: {
    ColorTile
  }
})
export default class Color extends Vue {
  @Prop({ required: true })
  value!: value

  @Prop({ required: true })
  hint!: string

  get color (): string {
    return this.value
  }

  set color (v: string) {
    this.input(v)
  }

  @Emit()
  input (v: value): value {
    return v
  }
}
