



































import draggable from 'vuedraggable'
import { Component, Vue, Prop } from 'vue-property-decorator'

interface ListItem {
  icon: string
  id: number
  key: string
  name: string
}

@Component({
  components: {
    draggable
  }
})
export default class DraggableWidgetList extends Vue {
  @Prop({ required: true })
  header!: string

  @Prop({ required: true })
  widgets!: ListItem[]
}
